.footer {
    background-color: #181a1c;
    width: 100%;
    text-align: left;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 16px;
    padding: 50px;
    margin-top: 0px;
}
  
.footer .footer-left,
.footer .footer-center,
.footer .footer-right {
    display: inline-block;
    vertical-align: top;
}
  
  
/* footer left*/
  
.footer .footer-left {
    width: 33%;
    padding-right: 15px;
}
  
.footer .about { 
    line-height: 20px;
    color: #ffffff;
    font-size: 13px;
    font-weight: normal;
    margin: 0;
}
  
.footer h3 {
    color: #ffffff;
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 20px;
}
  
.footer .icons {
    margin-top: 25px;
}
  
.footer .icons a {
    margin-top: -15px;
    display: inline-block;
    width: 30px;
    height: 27px;
    cursor: pointer;
    background-color: #33383b;
    border-radius: 10px;
    font-size: 20px;
    color: #ffffff;
    text-align: center;
    line-height: 35px;
    margin-right: 10px;
    margin-bottom: 10px;
}
  
  
/* footer center*/
  
.footer .footer-center {
    width: 30%;
    text-align: center;
}
.footer .footer-center div p{
    font-size: 1.5rem;
}
  
.footer .footer-center i {
    background-color: #33383b;
    color: #ffffff;
    font-size: 25px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    margin: 10px 15px;
    vertical-align: middle;
}
  
.footer .footer-center i.fa-envelope {
    font-size: 17px;
    line-height: 38px;
}
  
.footer .footer-center p {
    display: inline-block;
    color: #ffffff;
    vertical-align: middle;
    margin: 0;
    font-size: 23px;
}
  
.footer .footer-center p span {
    display: block;
    font-weight: normal;
    font-size: 14px;
    line-height: 2;
}
  
.footer .footer-center p a {
    color: #C5E6A6;
    text-decoration: none;
}
  
  
/* footer right*/
  
.footer .footer-right {
    width: 35%;
}
  
.footer h2 {
    color: #ffffff;
    font-size: 36px;
    font-weight: normal;
    margin: 0;
}
  
.footer h2 span {
    color: #C5E6A6;
    font-size: 75px;
    font-size: 4rem;
}
  
.footer .menu {
  
    color: #ffffff;
    margin: 20px 0 12px;
    padding: 0;
}
  
.footer .menu a {
    display: inline-block;
    line-height: 1.8;
    text-decoration: none;
    color: inherit;
}
  
.footer .menu a:hover {
    color: #C732FA;
}
  
.footer .name {
    color: #DE2C31;
    font-size: 14px;
    font-weight: normal;
    margin: 20px;
}
  
@media (max-width: 1085px) {
    .footer {
      font-size: 14px;
      padding: 5px;
    }
    .footer .footer-left,
    .footer .footer-center,
    .footer .footer-right {
      display: block;
      width: 100%;
      margin-bottom: 40px;
      text-align: center;
    }
}